import { TransactionTypeEnum } from 'src/enums/tranaction.type.enum';
import { freightCasesContainerArray, freightCasesCreateUpdateArray } from './../consts/freight.cases.const';
import { AlertService } from './alert.service';
import { EventEmitter, Injectable } from "@angular/core";
import { Router } from "@angular/router";
import { ICity } from "src/interfaces/i.city.interface";
import { IGeneralSetting } from "src/interfaces/i.general.setting.interface";
import { IState } from "src/interfaces/i.state.interface";
import { IUser } from "src/interfaces/i.user.interface";
import { carSizesArray, carSizesObject } from "src/consts/car.sizes.const";
import { HttpService } from './http/http.service';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { CustomValidatorsService } from './custom.validators.service';
import { CarSizesEnum } from 'src/enums/car.sizes.enum';
import { FileDragHandle } from 'src/interfaces/file.drag.handel.interface';
import { ITransaction } from 'src/interfaces/i.transaction.interface';
import { IPort } from 'src/interfaces/i.port.interface';
import { BranchesArray } from 'src/consts/branches.const';
import { FreitghtTypesArray } from 'src/consts/freight.types.const';
import { FreitghtTypesEnum } from 'src/enums/freitght.types.enum';

@Injectable()
export class FreightService {
  clinets: IUser[] = [];
  freightCases = freightCasesCreateUpdateArray;
  freightCasesToChange = freightCasesContainerArray;
  transactionFreightTypes = FreitghtTypesArray; 
  commissionPurchase = [40,50,60,70,80,90,100,110,120,130,140,150];
  freitghtTypesEnum = FreitghtTypesEnum;
  buySources: IGeneralSetting[];
  states: IState[] = [];
  allStates: IState[] = [];
  cities: ICity[] = [];
  freightPorts: IPort[] = [];
  arrivalPorts: IPort[] = [];
  carSizes = carSizesArray;
  navigationalWayes: IGeneralSetting[];
  isFormSubmitError: boolean = false;
  isRequestingToServer: boolean = false;
  carPicture = {} as FileDragHandle;
  defaultImageUrl: string = "/assets/img/car_default_img.jpg";
  isCreate: boolean;
  /** Deleted Item Event*/
  onDeletedItem: EventEmitter<string> = new EventEmitter<string>();
  onUnDeletedItem: EventEmitter<string> = new EventEmitter<string>();
  form: FormGroup = new FormGroup({});
  filterForm = new FormGroup({});
  freightsSelected: ITransaction[] = [];

  isAllowEnterClinetManually: boolean;
  branches = BranchesArray;
  isRequestingToServerToGetItems: boolean = false;
  constructor(private router: Router, public http: HttpService) { }
  /**
   * Close: Back To The Page
   */
  public closeModel() {
    this.isFormSubmitError = false;
    this.form.reset();
    this.router.navigate(['/freights']);
  }

  /** Init Create Update Form */
  initForm(transactionType = TransactionTypeEnum.freight) {
    this.form = new FormGroup({
      _id: new FormControl(),
      userId: new FormControl(null, [Validators.required]),
      carMark: new FormControl(null, [Validators.required]),
      freightCaseId: new FormControl(null, [Validators.required]),
      buySourceId: new FormControl(null, [Validators.required]),
      freightStateId: new FormControl(null, [Validators.required]),
      cityId: new FormControl(null, [Validators.required]),
      carSizeId: new FormControl(null, [Validators.required]),
      freightPortId: new FormControl(null, [Validators.required]),
      navigationalWayeId: new FormControl(null),
      arrivalPortId: new FormControl(null),
      carBodyNumber: new FormControl(null, [Validators.required]),
      notes: new FormControl(null),
      containerNumber: new FormControl(null),
      carPrice: new FormControl(0, [Validators.required, CustomValidatorsService.invalidNumber]),
      landFreightPrice: new FormControl(0, [Validators.required, CustomValidatorsService.invalidNumber]),
      maritimeFreightPrice: new FormControl(0, [Validators.required, CustomValidatorsService.invalidNumber]),
      commissionPurchase: new FormControl(0, [Validators.required, CustomValidatorsService.invalidNumber]),
      commissionTransfare: new FormControl(0, [Validators.required, CustomValidatorsService.invalidNumber]),
      swift: new FormControl(0, [Validators.required, CustomValidatorsService.invalidNumber]),
      sellPrice: new FormControl(0, [Validators.required, CustomValidatorsService.invalidNumber]),
      auctionFines: new FormControl(0, [Validators.required, CustomValidatorsService.invalidNumber]),
      storeFines: new FormControl(0, [Validators.required, CustomValidatorsService.invalidNumber]),
      titlePrice: new FormControl(0, [Validators.required, CustomValidatorsService.invalidNumber]),
      titleDifference: new FormControl(0, [Validators.required, CustomValidatorsService.invalidNumber]),
      landCost: new FormControl(0, [Validators.required, CustomValidatorsService.invalidNumber]),
      maritimeCost: new FormControl(0, [Validators.required, CustomValidatorsService.invalidNumber]),
      arrivalDate: new FormControl(null),
      refranceNumber: new FormControl(null),
      carPicture: new FormControl(null),
      lotOrStock: new FormControl(0, [Validators.required, CustomValidatorsService.invalidNumber]),
      titleCost: new FormControl(0, [CustomValidatorsService.invalidNumber]),

      trackLink: new FormControl(null, [CustomValidatorsService.invalidUrl]),
      transactionType: new FormControl(transactionType),
      maritimeFreightPriceIsPaid: new FormControl(false),
      //لان ممكن ان يختار بان يدخل المستخدم يدويا
      clinetName: new FormControl(null),
      clinetPhoneNumber: new FormControl(null),
      branchIndex: new FormControl(null),
      dueDollarCommissionTransfare: new FormControl(0, Validators.required),
      freightTypeId: new FormControl(null, Validators.required)
    });

    this.disapledControls();
    this.form.controls.freightPortId.valueChanges.subscribe(value => { this.updateMaritimFreightPrices(); this.loadStates() });
    this.form.controls.carSizeId.valueChanges.subscribe(value => this.updateMaritimFreightPrices());
    this.form.controls.freightStateId.valueChanges.subscribe(value => { this.loadStateCities(value); });
    this.form.controls.cityId.valueChanges.subscribe(value => { this.updateLandFreightPrices() });
    this.form.controls.userId.valueChanges.subscribe(value => {
      //Check If User Need To Enter Clinet Manually
      this.isAllowEnterClinetManually = value == "-1";
      if (this.isAllowEnterClinetManually) {
        this.form.controls.branchIndex.setValidators(Validators.required);
        this.form.controls.clinetName.setValidators(Validators.required);
        this.form.controls.clinetPhoneNumber.setValidators([CustomValidatorsService.phoneNumber, Validators.required]);
        this.form.controls.branchIndex.enable();
      } else {
        this.form.controls.branchIndex.clearValidators();
        this.form.controls.clinetName.clearValidators();
        this.form.controls.clinetPhoneNumber.clearValidators();
        this.form.controls.branchIndex.disable();
        //Select Branch Now
        if (value)
          this.form.controls.branchIndex.setValue(this.clinets.find(ce => ce._id == value).branchIndex);
      }
      this.form.controls.branchIndex.updateValueAndValidity();
      this.form.controls.clinetName.updateValueAndValidity();
      this.form.controls.clinetPhoneNumber.updateValueAndValidity();
    });
    this.isFormSubmitError = false;
    this.isRequestingToServer = false;
  }
  /**
   * Init Form For Delete
   */
  initDeleteForm() {
    this.form = new FormGroup({
      _id: new FormControl(null, Validators.required),
      refranceNumber: new FormControl(null, Validators.required),
      clinetName: new FormControl(null, Validators.required)
    });
    this.form.disable();
  }

  /** Init Form For Search */
  initFilterForm() {
    this.filterForm = new FormGroup({
      clinetName: new FormControl(),
      carMark: new FormControl(),
      freightCaseId: new FormControl(),
      buySourceId: new FormControl(),
      freightStateId: new FormControl(),
      cityId: new FormControl(),
      carSizeId: new FormControl(),
      freightPortId: new FormControl(),
      navigationalWayeId: new FormControl(),
      arrivalPortId: new FormControl(),
      carBodyNumber: new FormControl(),
      refranceNumber: new FormControl(),
      createdDate: new FormControl()
    });
    this.filterForm.controls.freightPortId.valueChanges.subscribe(value => { this.loadStates(value) });
    this.form.reset();
  }

  /**Disapled Controls */
  disapledControls() {
    this.form.controls.landFreightPrice.disable();
    this.form.controls.maritimeFreightPrice.disable();
    this.form.controls.refranceNumber.disable();
    this.form.controls.maritimeCost.disable();
    this.form.controls.branchIndex.disable();

  }

  /**Get Items To Select From Them */
  getItems(itemName?: string): void {
    this.isRequestingToServerToGetItems = true;
    let itemsNames = itemName ? [itemName] : [
      "clinets", "buySources",
      "states", "freightPorts", "arrivalPorts", "navigationalWayes"
    ];
    this.http.getSelectItems<any>(itemsNames).subscribe(res => {
      this.isRequestingToServerToGetItems = false;

      if (res.isSuccess) {
        itemsNames.forEach(itemName => {
          this[itemName] = [];
          if (itemName == "clinets") {
            this.clinets = [{
              _id: "-1",
              name: "<<إدخال زبون يدويا>>"
            } as any, ...res.result[itemName]];
          } else
            if (itemName == "states") {
              //Save All States Becuse Filter In It Affter User Select The Freight Port
              this.allStates = res.result.states;
              this.loadStates();
            }
            else
              this[itemName] = res.result[itemName];
        });
      }
      AlertService.showResponseMessage(res);
    }, error => {
      this.isRequestingToServerToGetItems = false;
      AlertService.canRequestError(error);
    })
  }


  /**Get Items To Select From Them  To Freights Page*/
  getItemsV2(itemName?: string): void {
    let itemsNames = itemName ? [itemName] : [
      "clinets", "buySources", "states", "freightPorts", "arrivalPorts", "navigationalWayes"
    ];
    this.http.getSelectItems<any>(itemsNames).subscribe(res => {
      if (res.isSuccess) {
        itemsNames.forEach(itemName => {
          this[itemName] = [];
          if (itemName == "clinets") {
            this.clinets = [{
              _id: null,
              name: "إدخال زبون يدويا"
            } as any, ...res.result[itemName]];
          } else
            if (itemName == "states") {
              //Save All States Becuse Filter In It Affter User Select The Freight Port
              this.allStates = res.result.states;
            }
            else
              this[itemName] = [... this[itemName], ...res.result[itemName]];
        });
      }
      AlertService.showResponseMessage(res);
    }, error => {
      AlertService.canRequestError(error);
    })
  }

  /**
   * Load States By Freight Port Id
   */
  loadStates(freightPortId: string = this.form.controls.freightPortId?.value || this.filterForm.controls.freightPortId?.value) {
    this.states = [];
    if (freightPortId && this.allStates.length)
      this.states = this.allStates.filter(st => st.freightPortId == freightPortId);
    this.loadStateCities();
  }

  /** Load State Cities */
  loadStateCities(stateId: string = this.form.controls.freightStateId?.value || this.filterForm.controls.freightStateId?.value): void {
    this.cities = [];
    if (this.states.length && stateId) {
      this.cities = this.states.find(m => m._id == stateId)?.cities || []
    }
    this.updateLandFreightPrices();
  }

  /**
* تحديث سعر الشحن البرى بناء على مدينة الشحن
  */
  updateLandFreightPrices(cityId: string = this.form.controls.cityId?.value) {
    let price = 0;
    if (cityId && this.cities.length) {

      let city = this.cities.find(ci => ci._id == this.form.controls.cityId.value);
      //سعر الشحن البرى ثابت على المدينة او الولايه كلها انما البحرى فهو على حجم السيارة
      price = +city?.landFreightPrice;
    }

    this.form.controls.landFreightPrice?.setValue(price || 0);
  }

  /** 
   * تحديث سعر الشحن البحري بناء على حجم السيارة وميناء الشحن
  */
  updateMaritimFreightPrices() {
    if (!this.form.controls.carSizeId.value || !this.form.controls.freightPortId.value) return;
    let port = this.freightPorts.find(st => st._id == this.form.controls.freightPortId.value);
    if (this.form.controls.carSizeId.value == CarSizesEnum.small)
      this.form.controls.maritimeFreightPrice.setValue(+port.maritimeFreightPriceToSmallCar || 0);
    if (this.form.controls.carSizeId.value == CarSizesEnum.medium)
      this.form.controls.maritimeFreightPrice.setValue(+port.maritimeFreightPriceToMediumCar || 0);
    if (this.form.controls.carSizeId.value == CarSizesEnum.big)
      this.form.controls.maritimeFreightPrice.setValue(+port.maritimeFreightPriceToBigCar || 0);
  }


  /**Select Freight */
  selectFreight(item: ITransaction) {
    item.IsSelected = !item.IsSelected;
    if (item.IsSelected) {
      this.freightsSelected.push(item);
    } else {
      this.freightsSelected.splice(this.freightsSelected.findIndex(f => f._id == item._id), 1);

    }
  }
}//End Class