import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { SharedComponents } from 'src/app/shared.components';
import { TransactionTypeEnum } from 'src/enums/tranaction.type.enum';
import { ITransaction } from 'src/interfaces/i.transaction.interface';
import { AlertService } from 'src/services/alert.service';
import { CurrentUserService } from 'src/services/authentcation/current.user.service';
import { FreightService } from 'src/services/freight.service';
import { SEOService } from 'src/services/seo.service';
import { SweetAlertService } from 'src/services/sweet.alert.service';

@Component({
  selector: 'app-create-freight',
  templateUrl: './create.freight.component.html',
  styles: [
  ]
})
export class CreateFreightComponent extends SharedComponents implements OnInit {

  titleResourcesKey = "createANewFreight";

  constructor(
    public u: CurrentUserService, private seo: SEOService,
    public freightService: FreightService,
    private router: Router,
    public sweetAlertService: SweetAlertService, private activatdeRouter: ActivatedRoute) { super() }

  ngOnInit(): void {
    this.titleResourcesKey = this.activatdeRouter.snapshot.queryParams.transactionType == TransactionTypeEnum.sectorSales ? "createANewSectorSale" : "createANewFreight";
    this.seo.updateSEO(this.titleResourcesKey);
    this.freightService.initForm(this.activatdeRouter.snapshot.queryParams.transactionType);
    this.freightService.getItems();
    this.freightService.defaultImageUrl = "assets/img/car_default_img.jpg";
    this.freightService.isCreate = false;
  }


  /**
    * Create New Item
    */
  onSubmit() {
    
    if (this.freightService.form.invalid) {
      this.freightService.isFormSubmitError = true;
      AlertService.errorMessage(this.u.r.youHaveSomeErrors);

      return;
    }
    this.freightService.isRequestingToServer = true;
    this.freightService.isFormSubmitError = false;
    let formData = new FormData();

    formData.append("values", JSON.stringify(this.freightService.form.getRawValue()));
    formData.append("carPicture", this.freightService.carPicture.file);
    this.freightService.form.disable();
    this.allSubscriptions.push(
      this.freightService.http.createNewFreight<ITransaction>(formData)
        .subscribe(res => {
          AlertService.showResponseMessage(res);
          if (res.isSuccess) {
            this.freightService.getItems();
            this.freightService.isRequestingToServer = false;

            this.freightService.form.enable();
            this.freightService.initForm();
            this.sweetAlertService.confirm(this.u.r.doYouWantEditCreatedItem, this.u.r.goToEdit, this.u.r.createNew).then(() => {
              this.router.navigate(['/freights', 'edit', res.result._id])
            })
          }
        }, error => {
          this.freightService.form.enable();
          this.freightService.isRequestingToServer = false;
          AlertService.canRequestError(error);
        }));
  }



}//End Class
