import { ResponseTypeEnum } from 'src/enums/response.type.enum';
import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import { SharedComponents } from 'src/app/shared.components';
import { AlertService } from 'src/services/alert.service';
import { CurrentUserService } from 'src/services/authentcation/current.user.service';
import { HttpService } from 'src/services/http/http.service';
import { FreightService } from 'src/services/freight.service';
import { ITransaction } from 'src/interfaces/i.transaction.interface';
import { FreightCasesEnum } from 'src/enums/freight.cases.enum';
import { freightCasesArray } from 'src/consts/freight.cases.const';

@Component({
  selector: 'app-add-freights-to-container',
  templateUrl: './freights.change.freight.case.component.html',
  styles: [
  ]
})
export class FreightsChangeFreightCaseComponent extends SharedComponents implements OnInit {
  constructor(public u: CurrentUserService, public service: FreightService, public http: HttpService,
    private activateRouter: ActivatedRoute) { super() }

  ngOnInit(): void {

    if (!this.service.freightsSelected.length) {
      AlertService.showResponseMessage({
        responseType: ResponseTypeEnum.error,
        message: this.u.r.youMustSelectFreightsFirst
      } as any);
      this.service.closeModel();
    }

    this.service.form = new FormGroup({
      freightCaseId: new FormControl(FreightCasesEnum.Loaded_in_a_container, Validators.required)
    });
  }

  /**
   * Delete Item
   */
  onSubmit() {
    if (this.service.form.invalid) {
      this.service.isFormSubmitError = true;
      return;
    }
    this.service.isRequestingToServer = true;
    this.allSubscriptions.push(
      this.service.http.changeFreightState<ITransaction>(this.service.form.controls.freightCaseId.value, this.service.freightsSelected.map(f => f._id))
        .subscribe(res => {
          AlertService.showResponseMessage(res);
          this.service.isRequestingToServer = false;
          if (res.isSuccess) {
            let caseName = freightCasesArray.find(c => c._id == this.service.form.controls.freightCaseId.value).name;
            this.service.freightsSelected.forEach(fe => {
              fe.IsSelected=false;
              fe.freightCaseName = caseName
            });
            this.service.freightsSelected=[];
            //Back To Component
            this.service.closeModel();
          }
        }, error => {
          this.service.isRequestingToServer = false;
          AlertService.canRequestError(error);
        }));
  }


}//End Class
