import { NgModule } from "@angular/core";
import { Routes, RouterModule } from "@angular/router";
import { CreateFreightComponent } from "src/app/back-end/freights/create-freight/create.freight.component";
import { DeleteFreightComponent } from "src/app/back-end/freights/delete-freight/delete.freight.component";
import { EditFreightComponent } from "src/app/back-end/freights/edit-freight/edit.freight.component";
import { FreightsChangeFreightCaseComponent } from "src/app/back-end/freights/freights-change-freight-case/freights.change.freight.case.component";
import { FreightsComponent } from "src/app/back-end/freights/freights.component";

  
const ROUTS: Routes = [
    {
        path: '', component: FreightsComponent,
        children: [
            {
                path: "delete/:targetId",
                component: DeleteFreightComponent,
            },
            {
                path: "chageFreightCase",
                component: FreightsChangeFreightCaseComponent,
            }
        ]
    },
    { path: 'create', component: CreateFreightComponent },
    { path: 'edit/:targetId', component: EditFreightComponent  },
];

@NgModule({
    imports: [RouterModule.forChild(ROUTS)],
    exports: [RouterModule]
})
export class FreightsRoutingModule {

}

